
// 本文件为标签api
import { API_URL } from '@/constants/';
import axios from './axios';

export default {
  get(data) {
    return axios.get(`${API_URL}/words`, {
      params: { ...data },
    }).then((res) => ({
      data: res.data,
    }));
  },
  getUnrelated(data) {
    return axios.get(`${API_URL}/words/unrelated`, {
      params: { ...data },
    }).then((res) => ({
      data: res.data,
    }));
  },
  add(data) {
    return axios.post(`${API_URL}/words`, data)
      .then((res) => res.data);
  },
  delete(data) {
    return axios.delete(`${API_URL}/words`, { data }).then((res) => res.data);
  },
};
