<template>
  <div class="keyword-content" v-loading="loading">
    <div class="title">
      {{title}}
    </div>
    <div class="content definition-content">
      <template v-for="(item, index) in datasheets">
        <div class="keyword definition-keyword" :key="index" @click="deleteFn(item)">
          {{item}}
          <i class="el-icon-circle-close"></i>
        </div>
      </template>
      <el-button class="button-add" icon="el-icon-plus"
                 @click="showFn" circle></el-button>
    </div>
    <el-dialog
      :title="title" width="560px"
      @close="dialogVisible = false"
      v-loading="dialogLoading"
      :append-to-body="true"
      :visible.sync="dialogVisible">
      <el-form
        :model="dialogForm" :rules="rules"
        label-width="80px" ref="dialogForm">
        <el-form-item label="词语" prop="kname">
          <el-input v-model="dialogForm.kname"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button @click="dialogVisible = false">关闭</el-button>
        <el-button type="primary" @click="addFn">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import words from '@/api/words';
import '@/assets/keyword.less';

export default {
  props: {
    label: String,
    title: String,
    type: String,
    keywords: Array,
    refresh: Boolean,
  },
  data() {
    return {
      visible: false,
      dialogVisible: false,
      loading: false,
      dialogLoading: false,
      dialogForm: {
        kname: '',
      },
      rules: {
        kname: [{
          required: true,
          message: '请输入词语',
          trigger: 'blur',
        }],
      },
    };
  },
  watch: {
    dialogVisible(val) {
      if (!val) {
        this.$refs.dialogForm.resetFields();
      }
    },
  },
  computed: {
    datasheets: {
      get() {
        return this.keywords;
      },
      set(val) {
        console.log(val);
        this.$emit('update:keywords', val);
      },
    },
  },
  methods: {
    deleteFn(kname) {
      this.$confirm(`确定要删除'${this.title}'的'${kname}'名词吗?`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      }).then(() => {
        this.loading = true;
        words.delete({
          label: this.label,
          keyinfos: [{
            type: this.type,
            kname,
          }],
        }).then(() => {
          this.loading = false;
          this.$message.success('删除成功!');
          if (this.refresh) {
            const index = this.datasheets.findIndex((item) => item === kname);
            this.datasheets.splice(index, 1);
          } else this.$emit('update');
        }).catch(() => {
          this.loading = false;
        });
      }).catch(() => {
        this.$message.info('已取消删除');
      });
    },
    showFn() {
      this.dialogVisible = true;
    },
    addFn() {
      this.$refs.dialogForm.validate((valid) => {
        if (valid) {
          this.dialogLoading = true;
          words.add({
            label: this.label,
            keyinfos: [{
              type: this.type,
              ...this.dialogForm,
            }],
          }).then(() => {
            this.dialogLoading = false;
            this.dialogVisible = false;
            this.$message.success('添加成功!');
            if (!this.refresh) this.$emit('update');
            else this.datasheets.push(this.dialogForm.kname);
          }).catch(() => {
            this.dialogLoading = false;
          });
        }
      });
    },
  },
};
</script>

<style scoped lang="less">
  .definition-content {
    min-height: 88px;
  }
  .definition-keyword {
    padding: 0 30px 0 15px;
    color: #fff;
    background-color: #1890ff;
    border: 1px solid #1890ff;
  }
</style>
