<template>
  <div>
    <div class="theme area">
      <div class="title">训练目标标签：{{label}}</div>
      <el-button @click="$router.back()">返回</el-button>
    </div>
    <div class="textarea area">
      <div class="title">文本内容</div>
      <p v-html="statement"></p>
    </div>
    <div class="area">
      <div class="title">非相关次组设置</div>
      <keyword-definition title="非相关词（若文本中出现该词组，则拒绝判断为此标签）" refresh
                          :label="label" type="overcome" :keywords.sync="phrase">
      </keyword-definition>
    </div>
  </div>
</template>

<script>
import keywordDefinition from '@/components/keywordDefinition.vue';

export default {
  props: {
    label: String,
    sentence: String,
    reason: String,
  },
  components: {
    keywordDefinition,
  },
  data() {
    return {
      statement: '',
      antonym: [],
      process: [],
      cache: [],
      phrase: [],
    };
  },
  watch: {
    process: {
      deep: true,
      handler(val) {
        this.statement = '';
        if (val.length) {
          val.forEach(({ word, prompt }) => {
            const colorClass = prompt ? 'prompt' : '';
            this.statement = `${this.statement}<span class="${colorClass}">${word}</span>`;
          });
        }
      },
    },
    phrase: {
      handler() {
        this.setStatementFn();
      },
      deep: true,
    },
  },
  mounted() {
    if (!this.label) this.$router.push('/reasoningRecord');
    if (this.reason !== 'no_noun') this.phrase.push(this.reason);
    else this.setStatementFn();
  },
  methods: {
    assemblyFn(word, prompt = false) {
      return { word, prompt };
    },
    serialization(keywords) {
      keywords.forEach((keyword) => {
        let arr = [];
        this.cache.forEach((item) => {
          const { word } = item;
          let subset;
          if (word.includes(keyword) && word.length !== keyword.length) {
            subset = word.split(keyword).map((child) => (this.assemblyFn(child)));
            const length = subset.length - 1;
            for (let i = 0; i < length; i += 1) {
              subset.splice(i * 2 + 1, 0, this.assemblyFn(keyword, true));
            }
          } else subset = [item];
          arr = [...arr, ...subset];
        });
        this.cache = arr;
      });
      this.process = this.cache;
    },
    setStatementFn() {
      if (!this.sentence) return;
      this.cache = [this.assemblyFn(this.sentence)];
      this.serialization(this.phrase);
    },
  },
};
</script>

